
// sign up screen

.signin-wrapper {
	.signin-inner {
		background-image: url("./images/login_bg.svg");
		background-size: cover;
		width: 100%;
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;

		.signin-form-wrapper {
			padding: 72px;
			width: 480px;
			background-color: white;
			border-radius: 25px;
			-webkit-border-radius: 25px;
			-moz-border-radius: 25px;
			-ms-border-radius: 25px;
			-o-border-radius: 25px;
			box-shadow: 0px 23px 194px rgba(137, 137, 137, 0.1);
			a {
				width: 150px;
				display: block;
				margin-bottom: 30px;
				img {
					width: 100%;
				}
			}
			h2 {
				margin-bottom: 5px;
			}
			p {
				margin-bottom: 15px;
			}
			.icon-input {
				position: relative;
				i {
					position: absolute;
					top: 12px;
					left: 15px;
				}
				input {
					padding-left: 40px;
					color: $text-black;
				}
			}
			.form-label {
				font-weight: 400;
				font-size: 14px;
				color: $text-black;
			}
			.btn {
                background-color: $blue-bg;
                font-size: 14px;
                color: white;
                margin-top: 20px;
                padding: 10px 0;
                border-radius: 6px;
                -webkit-border-radius: 6px;
                -moz-border-radius: 6px;
                -ms-border-radius: 6px;
                -o-border-radius: 6px;
}
		}
	}
}


.signin-wrapper .signin-inner.server {
  background-image: url("./images/server_login_bg.svg");

}



@media (max-width: 480.98px) {
    .signin-wrapper .signin-inner .signin-form-wrapper{
        padding: 40px 15px;
        width: 300px;
    }
  }


//   signup screen end